import React, { Suspense } from "react";
import { Canvas } from "@react-three/fiber";
import { OrbitControls, Preload } from "@react-three/drei";
import { /*Debug,*/ Physics } from "@react-three/cannon";
import { Color, Vector3 } from "three";
import Box from "./components/box";
import Plane from "./components/plane";
import CompoundBody from "./components/compoundBody";
import Dome from "./components/skyDome";
import "./App.css";

function App() {
  const totalPhysicBoxes: number[] = Array.from(
    { length: 250 },
    (e, i): number => i
  );
  // console.log(totalPhysicBoxes);
  return (
    <div id="canvas-container">
      <Canvas shadows camera={{ position: [0, 4, 45] }}>
        <OrbitControls maxPolarAngle={Math.PI / 2 - 0.1} maxDistance={50} />
        <Suspense fallback={null}>
          <ambientLight intensity={0.25} />
          <spotLight
            castShadow
            position={[40, 40, 30]}
            angle={0.35}
            penumbra={1}
            intensity={1.5}
            color={"#ff0000"}
          />
          <spotLight
            castShadow
            position={[0, 40, 45]}
            angle={0.35}
            penumbra={1}
            intensity={1.5}
            color={"#00ff00"}
          />
          <spotLight
            castShadow
            position={[-40, 40, 30]}
            angle={0.35}
            penumbra={1}
            intensity={1.5}
            color={"#0000ff"}
          />
          <Preload all />
          <Dome />
          <group position={[0, 5, 0]}>
            <Box
              color={new Color(0xff0000)}
              animated
              position={new Vector3(0, 0, 0)}
            />
            <Box
              color={new Color(0x0000ff)}
              reverseRotation
              animated
              wireframe
              position={new Vector3(0, 0, 0)}
            />
          </group>
          <group position={[0, 8.4, 0]}>
            <Box
              color={new Color(0xff0000)}
              animated
              wireframe
              position={new Vector3(0, 0, 0)}
            />
            <Box
              color={new Color(0x0000ff)}
              reverseRotation
              animated
              position={new Vector3(0, 0, 0)}
            />
          </group>
          <Physics>
            {/* <Debug scale={1} color="black"> */}
            {totalPhysicBoxes.map((index) => (
              <CompoundBody
                key={index}
                color={"#" + Math.floor(Math.random() * 16777215).toString(16)}
                transmission={Math.random()}
                position={[
                  Math.random() < 0.5 ? -Math.random() * 15 : Math.random() * 15,
                  (index + 50),
                  Math.random() < 0.5 ? -Math.random() * 15 : Math.random() * 15,
                ]}
                rotation={[
                  Math.random() * 360,
                  Math.random() * 360,
                  Math.random() * 360,
                ]}
              />
            ))}
            <Plane />
            {/* </Debug> */}
          </Physics>
        </Suspense>
      </Canvas>
    </div>
  );
}

export default App;
